// --- Helper for Rendering Markdown Bolding ---
const BOLD_REGEX = /\*\*(.*?)\*\*/g;

// Function to safely render bolded text
const renderDescription = (text: string) => {
    const parts = text.split(BOLD_REGEX);
    return parts.map((part, index) => {
      // Every odd index (1, 3, 5, ...) is a bolded segment
      return index % 2 === 1 ? <strong key={index} className="font-bold text-blue-300">{part}</strong> : part;
    });
};

// --- Component for a single company card ---
const CompanyCard: React.FC<{ company: Company }> = ({ company }) => {
  const cardRef = useRef<HTMLDivElement>(null)
  const [isVisible, setIsVisible] = useState(false)

  // Intersection Observer for fade-in animation on scroll
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
          observer.unobserve(entry.target)
        }
      },
      { threshold: 0.1 }
    )

    if (cardRef.current) {
      observer.observe(cardRef.current)
    }

    return () => {
      if (cardRef.current) {
        observer.unobserve(cardRef.current)
      }
    }
  }, [])

  const Icon = company.icon

  return (
    <div
      ref={cardRef}
      className={`group p-8 rounded-2xl shadow-xl transition-all duration-700 ease-out 
        ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}
        bg-black/50 border border-blue-500/10 hover:border-blue-500/50 hover:shadow-2xl hover:shadow-blue-500/30 cursor-pointer 
        backdrop-blur-sm
        ${company.id % 2 === 0 ? 'md:hover:translate-x-1' : 'md:hover:-translate-x-1'}` 
      }
    >
      <div className="mb-4 w-fit p-3 rounded-full bg-gradient-to-br from-blue-500/10 to-purple-500/10 text-blue-400 group-hover:from-blue-500/20 group-hover:to-purple-500/20 transition-all duration-300">
        {company.logo ? (
          <div className="relative w-9 h-9">
            <Image 
              src={company.logo} 
              alt={`${company.name} logo`}
              fill
              className="object-contain group-hover:scale-110 transition-transform duration-300" 
            />
          </div>
        ) : Icon ? (
          <Icon size={36} className="group-hover:scale-110 transition-transform duration-300" />
        ) : null}
      </div>

      <h2 className="text-3xl font-bold mb-2 bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">{company.name}</h2>
      <p className="text-white/70 mb-6 font-medium leading-relaxed">
        {renderDescription(company.description)}
      </p>

      <div className="mb-6 border-t border-white/10 pt-6">
        <h3 className="text-sm font-semibold text-blue-300 mb-3 uppercase tracking-wider">Roles</h3>
        <div className="flex flex-wrap gap-2">
          {company.roles.map((role) => (
            <span key={role} className="text-xs bg-gradient-to-r from-blue-500/10 to-purple-500/10 text-blue-300 px-3 py-1 rounded-full border border-blue-500/20 hover:border-blue-500/50 transition-colors">
              {role}
            </span>
          ))}
        </div>
      </div>

      <div className="border-t border-white/10 pt-6">
        <h3 className="text-sm font-semibold text-blue-300 mb-3 uppercase tracking-wider">Key Projects</h3>
        <ul className="space-y-3">
          {company.projects.map((project) => (
            <li key={project} className="flex items-center text-white/70 text-sm hover:text-blue-400 transition-colors group/item">
              <span className="w-2 h-2 bg-gradient-to-r from-blue-400 to-purple-400 rounded-full mr-3 flex-shrink-0 group-hover/item:scale-125 transition-transform"></span>
              {project}
            </li>
          ))}
        </ul>
      </div>
    </div>
  )
}

// --- Timeline Entry Component with Enhanced Interactivity ---
const TimelineEntry: React.FC<{ entry: any; idx: number }> = ({ entry, idx }) => {
    const TimelineIcon = entry.icon;
    const timelineRef = useRef<HTMLDivElement>(null);
    const [timelineVisible, setTimelineVisible] = useState(false);
    const [isHovered, setIsHovered] = useState(false);

    // Staggered timeline fade-in with cleanup fix
    useEffect(() => {
        let timeoutId: NodeJS.Timeout;
        const currentRef = timelineRef.current;
        
        const observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    // Stagger the visibility delay
                    timeoutId = setTimeout(() => setTimelineVisible(true), idx * 200);
                    observer.unobserve(entry.target);
                }
            },
            { threshold: 0.5 }
        );

        if (currentRef) {
            observer.observe(currentRef);
        }

        return () => {
            if (timeoutId) clearTimeout(timeoutId);
            if (currentRef) {
                observer.unobserve(currentRef);
            }
        };
    }, [idx]);

    return (
        <div 
            ref={timelineRef} 
            key={idx} 
            className={`flex gap-6 relative transition-all duration-700 ease-out 
                        ${timelineVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}
                        ${idx % 2 === 0 ? 'md:flex-row' : 'md:flex-row-reverse'}`}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
        >
            {/* Animated Connector Bar on Hover */}
            <div 
                className={`hidden md:block absolute left-1/2 top-0 h-full w-0.5 transform -translate-x-1/2 transition-all duration-300 ease-in-out z-0
                           ${isHovered ? 'bg-gradient-to-b from-blue-500 to-purple-500 shadow-blue-500/50 shadow-lg' : 'bg-gradient-to-b from-white/10 to-white/5'}`}
            />
            
            {/* Timeline Dot & Icon */}
            <div className={`absolute left-3 md:left-1/2 md:-translate-x-1/2 top-0 mt-2 z-10 p-2 rounded-full border-4 border-[#0a0a0a] transition-all duration-300 
                           ${isHovered ? 'bg-gradient-to-br from-blue-500 to-purple-500 scale-125 shadow-lg shadow-blue-500/40' : 'bg-gradient-to-br from-white/10 to-white/5'}`}>
                <TimelineIcon size={16} className={`transition-colors ${isHovered ? 'text-white' : 'text-blue-400'}`} />
            </div>

            {/* Year */}
            <div className="md:w-1/2 pt-2 flex-shrink-0">
                <span className={`text-lg font-bold bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent ${idx % 2 === 0 ? 'md:text-right md:pr-12 block' : 'md:text-left md:pl-12 block'}`}>
                    {entry.year}
                </span>
            </div>
            
            {/* Content Card */}
            <div className={`md:w-1/2 pb-12 pl-10 md:pl-0 relative z-10 ${idx % 2 === 0 ? 'md:text-left md:pl-12' : 'md:text-right md:pr-12'}`}>
                <div className="bg-black/50 backdrop-blur-sm border border-white/10 rounded-xl p-6 hover:border-blue-500/30 transition-all duration-300">
                  <h3 className="text-2xl font-bold mb-1 bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">{entry.company}</h3>
                  <p className="text-blue-300/80 mb-3 font-medium">{entry.role}</p>
                  <p className="text-white/70 transition-colors duration-300 leading-relaxed">
                      {renderDescription(entry.description)}
                  </p>
                </div>
            </div>
        </div>
    )
}

